* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  padding: 40px;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
}

ol,
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.country-container {
  display: flex;
  margin-bottom: 10px;
}

.country-title {
  margin-left: 10px;
}

.country-description {
  margin-top: 25px;
  font-size: 20px;
}

.country-container-info {
  display: flex;
  margin-bottom: 40px;
  font-size: 30px;
}

.country-card {
  margin-top: 25px;
}
